import React, { Component } from "react";
import "./App.css";
import iconurl from "./icon/爱心.png";
import bjtp1 from "./icon/1.jpg";
import bjtp2 from "./icon/2.jpg";
import bjtp3 from "./icon/3.jpg";
import bjtp4 from "./icon/4.jpg";
import bjtp5 from "./icon/5.jpg";
import bjtp6 from "./icon/6.jpg";
import Main from "./Main";
class App extends Component {
  state = {
    class: "bounceInLeft",
    show: false,
  };

  render() {
    const main = () => {
      if (this.state.show) return <Main />;
    };
    return (
      <div>
        <div className="rotateImg">
              <div className="box1"><img src={bjtp1} /></div>
              <div className="box2"><img src={bjtp2} /></div>
              <div className="box3"><img src={bjtp3} /></div>
              <div className="box4"><img src={bjtp4} /></div>
              <div className="box5"><img src={bjtp5} /></div>
              <div className="box6"><img src={bjtp6} /></div>
          </div>
        <div className={"envelope animated " + this.state.class}>
          <div className="triangle-down"></div>
          <img
            alt=""
            className="heart"
            src={iconurl}
            onClick={() => {
              this.setState({ class: "bounceOutRight", show: true });
            }}
          />
          <div className="triangle-up"></div>
          <div className="text">你收到一封信，点击查收！</div>
        </div>
        {main()}
      </div>
    );
  }
}

export default App;



