import React, { Component } from "react";
import $ from "jquery";
import url from "./audio/gbqq.mp3"; // 引入背景音乐文件

class Main extends Component {
  state = {
    date: {},
  };
  componentDidMount() {
    this.print();
    setInterval(() => {
      this.time(2019, 1, 11); // 纪念日
    }, 1000);
    var audio = document.getElementById("audio");
    setTimeout(() => audio.play(), 8500); // 背景音乐在页面加载后，延迟播放的时长，单位：毫秒。
  }
  print = () => {
    $.fn.autotype = function () {
      var _this = $(this);
      var str = _this.html();
      // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
      str = str.replace(/(\s){2,}/g, "$1");
      var index = 0;
      $(this).html("");
      var timer = function fn() {
        var current = str.slice(index, index + 1);
        // html标签完整输出,如：<p>
        if (current === "<") {
          index = str.indexOf(">", index) + 1;
        } else {
          index++;
        }
        //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
        if (index < str.length - 1) {
          //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
          _this.html(str.substring(0, index) + (index & 1 ? "_" : ""));
        } else {
          _this.html(str.substring(0, index));
          clearTimeout(timer);
        }
        setTimeout(fn, 200);
      };
      // 延迟1s开始
      setTimeout(timer, 1000);
    };
    $("#autotype").autotype();
  };
  time = (year, month, day) => {
    var dateNow = new Date();
    var dateJNR = new Date(year, month - 1, day);
    // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
    var d = parseInt((dateNow - dateJNR) / (24 * 3600 * 1000));
    var hour = parseInt(((dateNow - dateJNR) / (3600 * 1000)) % 24);
    var minute = parseInt(((dateNow - dateJNR) / (1000 * 60)) % 60);
    var second = parseInt(((dateNow - dateJNR) / 1000) % 60);
    this.setState({
      date: { d: d, hour: hour, minute: minute, second: second },
    });
  };
  render() {
    const date = () => {
      if (this.state.date.d !== undefined) {
        const { d, hour, minute, second } = this.state.date;
        return (
          <p>
            我们已经一起走过了: <span className="date-text"></span> 天 <span className="date-text"></span> 小时 <span className="date-text"></span> 分 <span className="date-text"></span> 秒{" "}
          </p>
        );
      }
    };
    return (
      <div className="App animated bounceInLeft">
        <div className="date">{date()}</div>
        <div id="autotype">
          <h1 style={{ fontWeight: 900 }}>哈喽！小王同学！</h1>
          <p>在煽情开始之前，先放首歌当背景音乐吧！Music!</p>
          <p>记得我们初次相识，是我的朋友在抖音要过来的你微信，我没有想到他真的要到了，还记得第一句话我说的是好激动，是真的意外。</p>
          <p>加上之后，就是每天简单聊几句，但是给我的感觉是你有点难相处，也不敢开玩笑。再后来就是我朋友说他要加，哪天晚上就是顺着这个话题，聊了很多也是从那个时候开始称得上真正的去了解你。</p>
          <p>再后来，你把你的故事讲给我听，我能明白其实你内心很小女，只是过往伤人太深。其实好多事，我都记得。例如：我记得第一次聊天你还给我分享了漯河景色。第一次评论的你朋友圈好像是，你那天发的文案是如果你没见过我，我就是美女，如果你见过我就当我没说。我评论了“我没见过”，你回复我了，那就是美女哈哈哈。</p>
          <p>其实在你身上我能看到，你的努力这一点我很欣赏。有时候我可能会讲很多大道理，希望你别厌倦哈哈哈，你说过感情不是生活的全部，我说一个人可以走的很快，但是两个人可以走的更远。</p>
          <p>虽然我们素未谋面，但是很快我们应该就可以初见了。也许我们会写下新的故事，我知道那南京很远，哎这一点我不能承诺你什么，因为我也许会在南京待两年三年，目前还没想好。</p>
          <p>最后祝我们共同努力！看当下！想未来！</p>
          <div style={{ textAlign: "right" }}>
            <p>来自南京的boy</p>
            <p>2022年09月06日</p>
          </div>
        </div>
        <audio id="audio" src={url}></audio>
      </div>
    );
  }
}
export default Main;



